import React, {useContext} from 'react';
import VideoGalleryItem from "./video-gallary-item.page";
import Video1 from './assets/video/human_feeding_the_little_squirrel (1080p).mp4'
import Video2 from './assets/video/human_feeding_the_little_squirrel (1080p).mp4'
import Video3 from './assets/video/human_feeding_the_little_squirrel (1080p).mp4'
import {SurveyContext} from "./surveyContext";
import {Link} from "react-router-dom";

const videos = [
    {title: 'Video 1', url: Video1},
    {title: 'Video 2', url: Video2},
    {title: 'Video 3', url: Video3},
];

const VideoGallery = () => {
    const {surveyCompleted} = useContext(SurveyContext);
    return (
        <div className="grid lg:grid-cols-3 grid-cols-2 items-center justify-center h-screen">
            {surveyCompleted ? (
                videos.map((video, index) => (
                    <VideoGalleryItem key={index} video={video}/>
                ))) :

                <div className={"h-screen flex items-center justify-center "}>
                    <div className="bg-white rounded-lg shadow-md p-8">
                        <h2 className="text-2xl font-bold mb-4">Access Denied</h2>
                        <p className="text-gray-700">
                            Please complete the survey form to gain access to the video content.
                        </p>
                        <Link to={'/'}>
                            <button className="mt-4 bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded">
                                Go to Survey
                            </button>
                        </Link>
                </div>


            </div>}
        </div>
    );
};

export default VideoGallery;