import React, {useContext, useState} from "react";
import {useNavigate} from "react-router-dom";
import Logo from "./assets/images/logo.png";
import {SurveyContext} from "./surveyContext";
import {useForm} from "react-hook-form";

const SurveyEntry = () => {
    const navigate = useNavigate();
    const [email, setEmail] = useState(null)
    const {setSurveyCompleted} = useContext(SurveyContext);
    const {register, handleSubmit, watch, formState: {errors}} = useForm();

    const onSubmit = async (e) => {
        const email = e?.email
        try {
            const response = await fetch('https://admin-panel.masscom-ci.com/api/check_email', {
                method: 'POST', body: JSON.stringify({email}),
            });

            if (response.ok) {
                setSurveyCompleted(true)
                navigate({
                    pathname: '/survey', search: `?email=${e?.email}`,
                });
            } else {
                alert("Dsol! vous avez dj fait l'enqute.")
                setSurveyCompleted(true)
                navigate({
                    pathname: '/video'
                });
            }
        } catch (error) {
            console.log(error?.data?.message)
            alert(error?.data?.message);
        }

    }

    return (<>
        <nav style={{textAlign: "center"}}>
            <img src={Logo} style={{width: '200px', height: "200px", margin: "auto"}} alt={"logo"}/>
        </nav>
        <div className="quiz-container">
            <p>Entrez votre Email ou numro et Accdez  nos films / Vidos / Liste de musique </p>
            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={"flex flex-col my-4"}>
                    <label htmlFor="email" className={"font-bold"}>Email ou numro et Accdez</label>
                    <input {...register("email", {required: true})} type="email"
                           onChange={(e) => setEmail(e.target.value)} id="email" name="email"
                           placeholder=""/>
                    {errors.email && <span className={"text-red-400"}>Ce champ est obligatoire</span>}
                </div>

                <button type="submit">Submit</button>
            </form>
        </div>

    </>)
}

export default SurveyEntry